body {
  box-sizing: border-box;
  margin: 0;
  font-family: Roboto, Arial, sans-serif;
}

.header {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header_logo {
  text-decoration: none;
  display: flex;
}

.header_logo_img {
  width: 40px;
  height: 40px;
  margin: 10px auto;
}

.nav {
  display: flex;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  margin: 0 0 0 20px;
  display: flex;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__link {
  color: #060b35;
  padding: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  transition: color, all .3s;
  position: relative;
}

.nav__link:hover, .is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  z-index: 1;
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.catalog {
  --column-count: 1;
  grid-template-rows: 2fr;
  grid-auto-flow: row;
  grid-template-columns: repeat(var(--column-count), 200px);
  box-sizing: border-box;
  justify-content: center;
  justify-items: center;
  gap: 46px 48px;
  margin: auto;
  padding: 50px 0;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    --column-count: 2;
  }
}

@media (width >= 768px) {
  .catalog {
    --column-count: 3;
  }
}

@media (width >= 1024px) {
  .catalog {
    --column-count: 4;
  }
}

.product-card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 200px;
  transition: transform .3s;
  display: flex;
}

.product-card:hover {
  transform: scale(1.2);
}

.product-card:hover .product-name {
  color: #34568b;
}

.product-card__image {
  box-sizing: border-box;
  background-image: url("imac.202c52ff.jpeg");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  width: 160px;
  height: 134px;
  margin: 32px 19px 0;
}

.product-card__content {
  margin: 40px 16px 16px;
  font-family: Roboto, sans-serif;
}

.product-name {
  color: #060b35;
  margin: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.product-code {
  color: #616070;
  margin: 4px 0 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.product-rating {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin: 16px 0 0;
  display: flex;
}

.product-rating__reviews {
  color: #060b35;
  margin: 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.stars {
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 4px;
  display: flex;
}

.stars__star {
  background-image: url("star-active.edda4c43.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:last-of-type {
  background-image: url("star.822d561e.svg");
}

.product-price {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin: 24px 0 0;
  display: flex;
}

.product-price__text {
  color: #616070;
  margin: 0;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.product-price__value {
  color: #060b35;
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.buy-button {
  box-sizing: border-box;
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  margin: 16px 0 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition: background-color, all .3s;
  display: flex;
}

.buy-button:hover {
  color: #00acdc;
  background-color: #fff;
}
